// Footer
.c-footer__container{
  background: $color-grey-light;
  box-shadow: inset 0 1px 0 0 $color-grey-light;
  padding: $spacing-xl 10px;
  margin-top: $spacing-xl;
}
.c-footer__grid{
  margin: 0 auto;
  max-width: $max-container-width;
}
.c-footer__social a{
  display: inline-block;
  margin: 0 $spacing-s $spacing-m 0;
}
.c-footer__contact__location{
  margin-bottom: $spacing-m;
}

@media screen and (max-width: $mobile-breakpoint-width) {
  .c-footer__container{
    padding: $spacing-l 0;
    margin-top: $spacing-l;
  }
  .c-footer__grid{
    justify-content: center;
    padding: 0 20px;
    text-align: center;
  }
  .c-footer__social{
    max-width: 100%;
    order: 2;
  }
  .c-footer__social a{
    margin: $spacing-m $spacing-s $spacing-m 0;
  }
}



