// Carousel
.c-carousel__container{
  display: flex;
  margin: 0 auto;
  max-width: $max-container-width - 10px; //-10 to align with main items
  overflow-x: hidden;
}
.c-carousel__item{
  margin: 0 5px;
  border-radius: $base-radius;

  &:first-of-type{
    margin: 0 5px 0 0;
  }
}



