// Thmbnails
.c-thumbnail__container{
  margin: 0 auto $spacing-m auto;
}
.c-thumbnail__item{
  margin: 0 auto 20px auto;
  width: 100%;
}
.c-thumbnail__image{
  border-radius: $base-radius;
  width: 100%;
}
.c-thumbnail__heading{
  @include body-typography;
  font-weight: bold;
  margin: $spacing-s 0 5px 0;
}
.c-thumbnail__excerpt{
  margin-bottom: $spacing-s;
}



