.c-page__title{
  @include base-font-family
  font-size: 24px;
  color: $color-black;
  letter-spacing: -0.4px;
  text-align: center;
  margin: $spacing-l 0;
}
.c-page__divider{
  border: none;
  border-bottom: 1px dashed $color-blue-dark;
  margin: $spacing-l auto;
  max-width: 170px;
}

@media screen and (max-width: $mobile-breakpoint-width) {
  .c-page__divider{
    margin: 40px auto;
  }
}



